import vueLoadingPage from './vue-loading-page.vue'

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(vueLoadingPage);
}

const loadingPage = {
    install(Vue, options){
        Vue.component(vueLoadingPage.name, vueLoadingPage)
    }
}

export default loadingPage


